:root{
    --yellow: #fec445;
    --red: #fe2c55;
}


.recipe__Link{
    color: black;
    text-decoration: none;
}

.recipes-global__container {
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
    flex-wrap: wrap;
}

.recipe__container {
    font-family: 'Poppins', sans-serif;
    position: relative;
    /* width: 16em; */
    width: 290px;
    margin: 1em 10px;
    background-color: #F0F9F9;
    /* font-size: 2rem; */
    transition: all ease .15s;
    cursor: pointer;
}

.recipe__container:hover {
    transform: translate(-2px,-2px);
    box-shadow: 5px 5px 0 var(--yellow);
}

img {
    width: 100%;
}

.recipe__text-container{
    padding: 0 1em 0.3em 1em;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.recipe__meal-type{
    font-size: 0.7em;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 2.2px;
    color: #b5460f;
}

.recipe__tittle{
    font-size: 1.3em;
    line-height: 1.3em;
    letter-spacing: normal;
    font-weight: 600;
    margin-top: 0;
    margin-bottom: 2em;
}

.recipe__footer {
    font-size: 0.7em;
}

.recipe__cuisine-type{
    left: 1.5em;
    color: #555;
    text-transform: uppercase;
    position: absolute;
    bottom: 5px;
}
